	module.exports = {"data":{"markdownRemark":{"html":"<p>Pair programming is where two people code together on one computer.</p>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/f/f68cff8b5354f548f9d680369891ece79b8a154f.jpg\"></p>\n<p>Pair programming encourages you to discuss different approaches to solving problems, and to keep one another other motivated. The result is better code than either of you could have written by yourselves.</p>\n<p>Because of its benefits, many engineers pair program full time. And it’s the best way to learn coding. Thanks to tools that allow two people to share mouse and keyboard inputs, you can pair program with a friend without needing to be in the same room.</p>\n<p>You can experience these benefits by pair programming with other Free Code Camp students on our coding challenges. Eventually, you’ll work with people at nonprofits to build real-life software solutions.</p>","fields":{"slug":"/miscellaneous/learn-about-pair-programming/"},"frontmatter":{"title":"Learn About Pair Programming"}}},"pathContext":{"slug":"/miscellaneous/learn-about-pair-programming/"}}

